<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";
import { mapActions } from 'vuex';

import axios from 'axios';
import AuthenticationService from '@/services/AuthenticationService';

export default {
    name: "ConsoleUsers",
    data(){
        return {
            memberTabView: true,
            profile_picture: '',
            filter: '',
            NewUserView: false,
            User: null,
            first_name: '',
            fields: [
                {
                    key: 'first_name',
                    sortable: true
                },
                {
                    key: 'last_name',
                    sortable: true
                },
                {
                    key: 'email',
                    sortable: true
                },
                {
                    key: 'tel',
                    sortable: true
                },
                {
                    key: 'action',
                    sortable: false
                },
                // {
                //     key: 'age',
                //     label: 'Person age',
                //     sortable: true,
                //     // Variant applies to the whole column, including the header and footer
                //     variant: 'danger'
                // }
            ],
            users: this.$store.getters.users.filter(user => user.role !== 'admin'),
            admins: this.$store.getters.users.filter(user => user.role === 'admin' && user.id !== 1),
            users_arr: [],
            form: {
                first_name: '', last_name: '', email: '', password: '1234567890', tel: '',
                campus_home_address: '', home_address: '', state: '',
                dob: '', gender: '', dept: '', level: '', programme: '',
                profile_picture: '', units: '', role: 'member', email_verified: 1,
                status: 1
            },
            row: {
                toggleDetails: null
            },
            perPage: 20,
            currentPage: 1,
            
        }
    },
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    computed: {
        
    },
    methods: {
        ...mapActions(['deleteUser', 'createUser', 'user_account', 'update_account', 'fetchUsers']),
        async handleNewUser(){
            await this.createUser(this.form);
            await this.refreshUsers();
            this.NewUserView = false;
        },
        async newUserView(){
            this.NewUserView = true;
            this.User = null;
        },
        async loadUserView(){
            this.NewUserView = false
            this.User = null;
        },
        async filterUser(id){
            this.User = this.users.filter(user => user.id === id);
            if (this.User.length > 0) {
                this.User = this.User[0];
                this.NewUserView = null;
            }
        },
        async filterAdmin(id){
            this.User = this.admins.filter(user => user.id === id);
            if (this.User.length > 0) {
                this.User = this.User[0];
                this.NewUserView = null;
            }
        },
        async refreshUsers(){
            await this.fetchUsers();
            this.users = this.$store.getters.users.filter(user => user.role !== 'admin');
            this.admins = this.$store.getters.users.filter(user => user.role === 'admin' && user.id !== 1 );
        },
        async submitForm(){
            try{
                await this.update_account({id: this.User.id, user: this.User});
                await this.fetchUsers();
            }
            catch(err){
                console.log(err);
            }
        },
        async handleDeleteUser(id){
            let user = this.users.filter(user => user.id === id);
            // console.log(user);
            if (user.length > 0) {
                if (user[0].profile_picture === null || user[0].profile_picture === '') {
                    await this.deleteUser(id);
                    await this.refreshUsers();
                }
                else{
                    const response = await AuthenticationService.removeImage({old_image: user[0].profile_picture});
                    if(response.data.error === 0){
                        await this.deleteUser(id);
                        await this.refreshUsers();
                    }
                }
            }
        },
        async handleDeleteAdmin(id){
            let user = this.admins.filter(user => user.id === id);
            // console.log(user);
            if (user.length > 0) {
                if (user[0].profile_picture === null || user[0].profile_picture === '') {
                    await this.deleteUser(id);
                    await this.refreshUsers();
                }
                else{
                    const response = await AuthenticationService.removeImage({old_image: user[0].profile_picture});
                    if(response.data.error === 0){
                        await this.deleteUser(id);
                        await this.refreshUsers();
                    }
                }
            }
        },
        async changeImage(){
            this.profile_picture = this.$refs.profile_picture.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'images');
            formData.append('tags', 'unsigned upload, images');
            formData.append('file', this.profile_picture);

            let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);

            if (response.data.secure_url !== null) {
                this.User.profile_picture = response.data.secure_url;
                let old_image = this.User.profile_picture;  
                await this.update_account({id: this.User.id, user: this.User});

                if (old_image !== null) {
                    await AuthenticationService.removeImage({old_image});
                }

            }
        }
    }

}
</script>

